/**
 * Importaes internas necessrias para este componente.
 * - React: permite a criao e manipulao de componentes React.
 * - useState: hook que possibilita a adio de estado a componentes funcionais.
 * - useEffect: hook utilizado para executar efeitos colaterais em componentes funcionais, como aes aps renderizaes.
 * - Loading: componente responsvel pela exibio de um indicador de carregamento.
 * - Chart: importao da classe Chart do mdulo "chart.js/auto" para a renderizao de grficos.
 */
import React, { useState, useEffect, useRef, useContext } from "react";
import { Chart } from "chart.js/auto";

/**
 * Importao de um componente que atuar como um elemento do DOM neste componente.
 * - Div: importao do componente Div do mdulo "../styles/dashboardPie".
 */
import { Div, Div2 } from "../../styles/dashboardPie";
import { TicketContext } from "../../context/TicketContext";

let myChart = null;

export default function DashBoardPie({ sector, clss }) {
  /**
   * Variveis de estado utilizadas neste componente.
   * - dataPie: estado que armazena os dados para o grfico de pizza.
   */
  const [dataPie, setDataPie] = useState("");

  const { totalTickets, setTotalTickets } = useContext(TicketContext);

  const timeoutRef = useRef(null);

  /**
   * - Acionado ao inicializar o componente para buscar dados para o dashboard de pizza.
   * - Utiliza uma funo assncrona para realizar a requisio dos dados.
   * - Realiza uma requisio GET para obter os dados do dashboard de pizza.
   * - Se a resposta for 210, define o estado de loading como verdadeiro.
   * - Se a requisio for bem-sucedida, define os dados recebidos como estado e define loading como falso.
   * - Em caso de erro, imprime o erro no console.
   * - A funo de limpeza da useEffect no  necessria neste caso, pois no h nada para limpar.
   * - O useEffect  executado apenas uma vez aps a montagem do componente devido  dependncia vazia [].
   */
  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(`get-dash-board-pie/${sector}`, {
          method: "GET",
          headers: {
            Accept: "application/json",
            "Cache-Control": "no-cache",
          },
        });

        const data = await response.json();
        setDataPie(data.data);

        return dataPie;
      } catch (err) {
        return console.error(err);
      }
    };

    return fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (dataPie && dataPie.length !== 0) {
      try {
        const dash = document.getElementById("dashpie");
        if (myChart) {
          myChart.destroy();
        }

        var total = dataPie.at(0);
        setTotalTickets(total);

        var dataPieCopy = dataPie; // Cria uma cpia do array

        dataPieCopy = dataPieCopy.slice(1);

        myChart = new Chart(dash, {
          type: "pie",
          data: {
            labels: [
              "Chamados em Aberto",
              "Chamados Finalizados",
              "Chamados em Aguardo",
              "Chamados Urgentes(mais de 7 dias aberto)",
            ],
            datasets: [
              {
                data: dataPieCopy,
                backgroundColor: ["#ffd60a", "#38b000", "#f9f9f9", "#d00000"],
                hoverOffset: 4,
              },
            ],
          },
        });

        dash.style.display = "block";

        return CallNewBar();
      } catch (err) {
        return console.log(err);
      }
    }
    return;
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dataPie]);

  function CallNewBar() {
    // Se j houver um timeout ativo, no cria um novo
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current);
    }

    timeoutRef.current = setTimeout(() => {
      FetchPie();
      timeoutRef.current = null; // Reseta aps a execuo
    }, 60000);
  }

  function FetchPie() {
    const fetchData = async () => {
      try {
        const response = await fetch(`get-dash-board-pie/${sector}`, {
          method: "GET",
          headers: {
            Accept: "application/json",
            "Cache-Control": "no-cache",
          },
        });

        const data = await response.json();

        if (JSON.stringify(data.data) !== JSON.stringify(dataPie)) {
          setDataPie(data.data);
        }
        CallNewBar();
        return;
      } catch (err) {
        if (err instanceof SyntaxError) {
          return (window.location.href = "/login");
        }
        return console.error(err);
      }
    };

    return fetchData();
  }

  return (
    <Div>
      <Div2>
        <canvas id="dashpie" className="hidden"></canvas>
      </Div2>
      <div className="d-flex w-100 text-center justify-content-center">
        <p className={clss}>{totalTickets}</p>
      </div>
    </Div>
  );
}
